import uPopup from '../../../components/basic-component/u-popup/u-popup.vue';
export default {
    data() {
        return {
            animationDuration: '10s',
            animationPlayState: 'paused',
            showText: '',
            visibleSync: false,
            showDrawer: false,
            timer: null
        }
    },
    components: {
        uPopup
    },
    props: {
        content: String,
        bgColor: {
            type: String,
            default: '#f67f79'
        },
        icon: {
            type: String,
            default: ''
        },
        name: {
            type: String,
            default: '公告'
        },
        textColor: {
            type: String,
            default: '#ffffff'
        },
        headerUrl: {
            type: String,
            default: ''
        },
        btnText: {
            type: String,
            default: '我知道了'
        },
        btnTextColor: {
            type: String,
            default: '#ffffff'
        },
        btnColor: {
            type: String,
            default: '#ff4544'
        },
        btnRadius: {
            type: String,
            default: '40rpx'
        },
        btnWidth: {
            type: Number,
            default: 500
        }
    },
    watch: {
        content: {
            immediate: true,
            handler(newVal) {
                if (newVal) {
                    this.showText = newVal.replace(/[\r\n]/g,"");
                    this.$nextTick(() => {
                        this.initSize();
                    });
                }
            }
        }
    },
    methods: {
        initSize() {
            let query = [];
            let textQuery = new Promise(resolve => {
                uni.createSelectorQuery().in(this).select(`#u-notice-content`).boundingClientRect().exec(ret => {
                    this.textWidth = ret[0].width;
                    resolve();
                });
            });
            query.push(textQuery);
            Promise.all(query).then(() => {
                this.animationDuration = `${this.textWidth / uni.upx2px(100)}s`;
                this.animationPlayState = 'paused';
                setTimeout(() => {
                    this.animationPlayState = 'running';
                }, 10);
            });
        },
        take(data) {
            this.visibleSync = data;
        }
    },
    computed: {
        btnStyle: function() {
            return `width: ${this.btnWidth}rpx;color:${this.btnTextColor};background-color:${this.btnColor};border-radius: ${this.btnRadius};`
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.initSize();
        });
    }
}
